<template>
	<div id="goods-list">
		<public-header title="全部商品"></public-header>
		<div class="content">
			<van-row class="wh-100" v-if="categorys && categorys[selectCategoryId]">
				<van-col span="6" class="wh-100 tag-list">
					<div class="tag-item van-hairline--bottom" :class="{active: selectCategoryId == index}" v-for="(item, index) in categorys" :key="'c' + index">
						<div class="tag-inner" @click="onToggleCategory(item.id)">{{item.title}}</div>
					</div>
				</van-col>
					<van-col span="18" class="wh-100 bg-default goods-wrap">
						<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
							<div class="category-title">{{selectCategory.title}}({{selectCategory.goods_num}})</div>
							<div class="goods-list padding-10-lr" >
								<div class="goods-item van-hairline--bottom" v-for="(item, index) in goodsItem" :key="index">
									<div class="goods-info flex-lr">
										<div class="goods-thumb">
											<div class="hot" v-if="item.label">{{item.label}}</div>
											<div class="status" v-if="item.status != 1">已下架</div>
											<img class="wh-100" :src="item.thumb" alt="">
										</div>
										<div class="flex-tb-left info-inner">
											<div class="font-bold w-100 ellipsis font-15">{{item.title}}</div>
											<div class="c-gray flex font-13">
												<span>已售: {{item.sailed}}</span>
												<div class="sailed-tip" v-if="item.total == -1">不限库存</div>
												<span class="margin-10-l">库存: {{item.total}}</span>
												<div class="sail-limit" v-if="item.price < item.old_price">限时售卖</div>
											</div>
											<div class="flex font-13">
												<span class="c-info">外卖: <span class="font-bold">¥{{item.price}}</span></span>
												<span class="c-danger margin-10-l">店内: <span class="font-bold">¥{{item.ts_price}}</span></span>
											</div>
										</div>
									</div>
									<div class="btn-group">
										<div class="btn-item" @click="onChangeStatus('total', item, index)" v-if="item.total != 0">售罄</div>
										<router-link tag='div' class="btn-item" :to="util.getUrl({path: 'pages/goods/post', query: {id: item.id}})">编辑</router-link>
										<div class="btn-item" @click="onChangeStatus('status', item, index)">{{item.status == 1 ? '下架' : '上架'}}</div>
									</div>
								</div>
								<div class="no-data" v-if="goodsItem.length == 0">
									<img src="static/img/order_no_con.png" alt="">
									<p>暂无商品</p>
								</div>
								<div class="loaded" v-else>
									<div class="loaded-tips white">我是有底线的</div>
								</div>
							</div>
						</van-pull-refresh>
					</van-col>
			</van-row>
			<div class="action-bottom van-hairline--top">
				<router-link tag="div" :to="util.getUrl({path: 'pages/category/index'})" class="action-item">
					<i class="icon icon-list c-info"></i>
					管理分类
				</router-link>
				<router-link tag="div" class="action-item van-hairline--left" :to="util.getUrl({path: 'pages/goods/post'})">
					<i class="icon icon-roundadd c-info"></i>
					新建商品
				</router-link>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				goodsAll: {},
				goodsItem: [],
				categorys: {},
				category_goodsNum: {},
				selectCategoryId: 0,
				selectCategory: {}
			}
		},
		methods: {
			onLoad(cid){
				var that = this;
				if(that.goodsAll[that.selectCategoryId]) {
					this.goodsItem = that.goodsAll[that.selectCategoryId];
					return;
				}
				getData({
					vue: this,
					url: 'manage/goods/index/list',
					data: {
						cid: cid
					},
					success: (res) => {
						that.isRefresh = false;
						that.goodsItem = res.goods;
						that.selectCategoryId = res.cid;
						that.goodsAll[that.selectCategoryId] = res.goods;
						that.categorys = res.categorys;
						that.selectCategory = res.categorys[that.selectCategoryId];
						that.selectCategory.goods_num = res.goods_num;
						that.category_goodsNum[that.selectCategoryId] = res.goods_num;

					}
				});
			},
			onToggleCategory(cid) {
				this.selectCategoryId = cid;
				this.selectCategory = this.categorys[cid];
				this.selectCategory.goods_num = this.category_goodsNum[cid];
				this.onLoad(cid);
			},
			onPullDownRefresh() {
				delete(this.goodsAll[this.selectCategoryId]);
				this.onLoad(this.selectCategoryId);
			},
			onChangeStatus(type, goods, index) {
				var that = this;
				var id = goods.id;
				var cid = goods.cid;
				if(type == 'status') {
					var success_status = goods.status == 1 ? 0 : 1;
					var params = {
						url: 'manage/goods/index/status',
						confirm: goods.status == 1 ? '确定下架此商品吗?' : '确定上架此商品吗?',
						data: {
							id: id,
							value: success_status
						},
						success: function(res) {
							that.util.$toast(res);
							that.goodsAll[cid][index].status = success_status;
							that.goodsItem = that.goodsAll[cid];
						}
					};
				} else if(type == 'total') {
					var params = {
						url: 'manage/goods/index/turncate',
						confirm: '确定设置已售罄吗?',
						data: {id: id},
						success: function(res) {
							that.util.$toast(res);
							that.goodsAll[cid][index].total = 0;
							that.goodsItem = that.goodsAll[cid];
						}
					};
				}
				that.util.jspost(params);
			}
		},
		mounted(){
			this.onLoad();
		}
	}
</script>

<style>
	#goods-list .content{
		padding-bottom: 50px;
	}
	#goods-list .content .tag-list,#goods-list .content .goods-wrap{
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#goods-list .content .action-bottom{
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 50px;
		background-color: #fff;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		font-size: 15px;
	}
	#goods-list .content .action-bottom .action-item{
		width: 50%;
		text-align: center;
		padding: 7.5px 0;
		flex: 1;
	}
	#goods-list .tag-item{
		padding:15px 10px;
		color:#7a7a7a;
		font-size: 14px;
	}
	#goods-list .tag-item .tag-inner{
		text-align:center;
		overflow:hidden;
		display:-webkit-box;
		width:100%;
		line-height:17px;
		box-sizing:border-box;
		text-overflow:ellipsis;
		-webkit-box-orient:vertical;
		-webkit-line-clamp:2;
	}
	#goods-list .tag-item.active{
		color:#333;
		background-color:#fff;
		border-left:3px solid #33aafc;
	}
	#goods-list .category-title{
		padding: 10px;
		background-color: #f5f5f5;
		border-left: 3px solid #ccc;
		font-size: 14px;
		color: #7a7a7a;
	}
	#goods-list .goods-item{
		padding: 20px 0;
	}
	#goods-list .goods-item .goods-thumb{
		width: 60px;
		height: 60px;
		border-radius: 5px;
		overflow: hidden;
		position: relative;
	}
	#goods-list .goods-item .goods-thumb .hot{
		position: absolute;
		z-index: 10;
		top: 0;
		left: 0;
		height: 15px;
		line-height: 15px;
		background-color: #FF2D4B;
		color: #fff;
		padding: 0 5px;
		font-size: 11px;
		text-align: left;
	}
	#goods-list .goods-item .goods-thumb .status{
		position: absolute;
		z-index: 10;
		bottom: 0;
		left: 0;
		height: 15px;
		line-height: 15px;
		width: 100%;
		background-color: #999;
		color: #fff;
		font-size: 11px;
		text-align: center;
	}
	#goods-list .goods-item .info-inner{
		height: 60px;
		flex: 1;
		padding-left: 10px;
	}
	#goods-list .goods-item .info-inner .sailed-tip{
		padding: 1px 3px;
		color: #33aafc;
		border: 1px solid #33aafc;
		border-radius: 2px;
		font-size: 11px;
		margin:0 10px;
	}
	#goods-list .goods-item .info-inner .sail-limit{
		padding: 1px 3px;
		color: #535353;
		border: 1px solid #535353;
		border-radius: 2px;
		font-size: 11px;
	}
	#goods-list .btn-group{
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: flex-end;
		margin-top: 10px;
	}
	#goods-list .btn-group .btn-item{
		font-size: 14px;
		padding: 5px 10px;
		color: #33aafc;
		border: 1px solid #33aafc;
		border-radius: 3px;
		margin-left: 10px;
	}
</style>


